<template>
	<div id="awardSetting-main">
		<section class="awardModel">
			<h4>中奖模式</h4>
			<div>
				<label v-for="(item,index) in radioList" @click="chooseProbabilityMethod(item,index,item.code)">
					<input type="radio" name="probability" :checked="item.isChecked">
					{{item.name}}
				</label>
				<!-- <label>
					<input type="radio" name="probability">
					系统分配中奖概率
				</label> -->
			</div>
		</section>
		<div class="awardModelComponent">
			<router-view></router-view>
		</div>
	</div>
</template>

<script type="text/javascript">
	export default {
		data(){
			return {
				radioList:[
					{name:'自定义中奖概率',isChecked:true,code:0},
					{name:'系统分配中奖概率',isChecked:false,code:1}
				]
			}
		},
		mounted(){},
		methods:{
			chooseProbabilityMethod:function(item,index,code){
				this.radioList.forEach(data => {
					if(data.isChecked !== this.radioList[index].isChecked){
						data.isChecked = false;
					}
				});
				item.isChecked = !item.isChecked;
				if(code == "1"){
					this.$router.push({path:'/activity/awardSetting/systemProbability'});
				}else{
					this.$router.push({path:'/activity/awardSetting/customProbability'});
				}
			}
		}
	}
</script>

<style lang="scss" scoped>
	@import '../../../style/mixin';
	#awardSetting-main{
		.awardModel{
			h4{
				@include sc(0.7rem,#929292);
				height:1.8rem;
				line-height:1.8rem;
				padding:0 0.5rem;
			}
			div{
				height:2rem;
				line-height: 2rem;
				background:#fff;
				padding:0 0.5rem;
				label{
					@include sc(0.7rem,#333);
					&:not(:last-child){
						margin-right:1rem;
					}
					input[type="radio"]{
						position:relative;
						top:0.1rem;
					}
					input[type="radio"]:before{
						display: block;
						width: 0.6rem;
						height: 0.6rem;
						content: '';
						position: absolute;
						top: -0.16rem;
						left: -0.1rem;
						background: #fff;
						border: 1px solid #929292;
						text-align: center;
						line-height: 0.43rem;
						font-size: 1.2rem;
						border-radius: 50%;
					}
					input[type="radio"]:checked:before{
						display: block;
						width: 0.6rem;
						height: 0.6rem;
						content: '\2022';
						color: #F35353;
						position: absolute;
						top: -0.16rem;
						left: -0.1rem;
						background: #fff;
						border: 1px solid;
						text-align: center;
						line-height: 0.43rem;
						font-size: 1.2rem;
						border-radius: 50%;
					}
				}
			}
		}
	}
</style>


